import React from 'react'
import { Field, reduxForm } from 'redux-form'
import { createTextMask } from 'redux-form-input-masks'
import { Provider, Popover } from 'reakit'
import theme from 'reakit-theme-default'
import moment from 'moment'

import styles from '../Forms.module.scss'

import Button from '../../../ui/Button'

const validate = (values) => {
    const errors = {}
    if (!values.FIO) {
        errors.FIO = 'Required'
    } else if (values.FIO.length < 6) {
        errors.FIO = 'Слишком короткое ФИО'
    }
    if (!values.date_of_birth) {
        errors.date_of_birth = 'Required'
    } else if (!(moment(values.date_of_birth, 'DDMMYYYY').isValid())) {
        errors.date_of_birth = 'Введите правильную дату рождения'
    } else if (!(moment(values.date_of_birth, 'DDMMYYYY').isAfter(moment('1900', 'YYYY')))) {
        errors.date_of_birth = 'Вы не можете быть старше меня :)'
    }

    if (!values.INN) {
        errors.INN = 'Required'
    } else if (values.INN.length !== 12) {
        errors.INN = 'ИНН физического лица является последовательностью из 12 цифр'
    }

    if (!values.SNILS) {
        errors.SNILS = 'Required'
    } else if (values.SNILS.length !== 11) {
        errors.SNILS = 'СНИЛС состоит из 11 цифр'
    }

    return errors
}

const renderField = ({
    input, label, type, meta: { touched, error, warning }, placeholder,
}) => (
    <Provider theme={theme}>
        <div className={styles.Field}>
            <label className={styles.form__label}>
                <span>{label}</span>
                <input {...input} placeholder={placeholder} type={type} />
            </label>
            <div className={styles.error}>
                {touched && (
                    (
                        <Popover
                            fade
                            placement="right-end"
                            gutter={7}
                            color="red"
                            visible={typeof error !== 'undefined'}
                        >
                            {error}
                        </Popover>
                    )
                    || (
                        <Popover
                            fade
                            placement="right-end"
                            gutter={7}
                            color="red"
                            visible={typeof warning !== 'undefined'}
                        >
                            {warning}
                        </Popover>
                    )
                )}
            </div>
        </div>
    </Provider>
)

function MainForm({ handleSubmit, pristine }) {
    const DOBMask = createTextMask({
        pattern: '99.99.9999',
        placeholder: '_',
    })
    const snilsMask = createTextMask({
        pattern: '999-999-999 99',
        placeholder: '_',
    })

    return (
        <div>
            <div className={styles.forms}>
                <div className={styles.forms__title}>Профиль</div>

                <form
                    className={styles.form}
                    onSubmit={handleSubmit}
                >

                    <div className={styles.form__container}>

                        <Field
                            name="FIO"
                            label="ФИО"
                            component={renderField}
                            type="text"
                            placeholder="Иванов Иван Иванович"
                        />
                        <Field
                            name="date_of_birth"
                            label="Дата рождения"
                            component={renderField}
                            type="text"
                            placeholder="22.07.2001"
                            {...DOBMask}
                        />
                        <Field
                            name="INN"
                            label="ИНН"
                            component={renderField}
                            type="text"
                            placeholder="111111111111111"
                        />
                        <Field
                            name="SNILS"
                            label="СНИЛС"
                            component={renderField}
                            type="text"
                            placeholder="1111111111111111"
                            {...snilsMask}
                        />

                        <Button type="submit" disabled={pristine}>
                            Сохранить
                        </Button>
                    </div>


                </form>
            </div>

            <div className={styles.forms}>
                <div className={styles.forms__title}>Документ удостоверяющий личность</div>

                <form
                    className={styles.form}
                    onSubmit={handleSubmit}
                >

                    <div className={styles.form__container}>

                        <Field
                            name="type"
                            label="Тип документа"
                            component={renderField}
                            type="text"
                        />
                        <Field
                            name="seri"
                            label="Серия"
                            component={renderField}
                            type="text"
                        />
                        <Field
                            name="number"
                            label="Номер"
                            component={renderField}
                            type="text"
                        />
                        <Field
                            name="whomGiven"
                            label="Кем выдан"
                            component={renderField}
                            type="text"
                        />
                        <Field
                            name="unityCode"
                            label="Код подразделения"
                            component={renderField}
                            type="text"
                        />
                    </div>


                </form>
            </div>
        </div>
    )
}

const MainReduxForm = reduxForm({
    form: 'My_profile',
    destroyOnUnmount: false,
    onSubmit: (formData) => {
        // eslint-disable-next-line no-alert
        alert(`Вы отправили: \n\n${JSON.stringify(formData, null, 2)}`)
    },
    validate,
})(MainForm)

export default MainReduxForm
